<template>
    <div class="bk-info-container">

         <div ref="topBg" class="bank-top">
           <div class="card-img">
             <img ref="myImg" v-if="bankInfo.icon" :src="bankInfo.icon"  onerror="javascript:this.src='/static/images/bank_default.jpg';">
           </div>
            <div class="top-right">
              <div class="top">
                <span class="bank-name">{{bankInfo.name}}</span>
                <span class="bank-divider">|</span>
                <span v-if ="bankInfo.propaganda" class="rights">{{bankInfo.propaganda}}</span>
              </div>

              <div class="tag-class">
                <span class="tag1" v-if="bankInfo.tag1">{{bankInfo.tag1}}</span>
                <span class="tag2" v-if="bankInfo.tag2">{{bankInfo.tag2}}</span>
                <span class="tag3" v-if="bankInfo.tag3">{{bankInfo.tag3}}</span>
            </div>
            </div>

         </div>
        <div class="mid-class" v-if="showMid">
           <div>
              <card-info-segment :desc="bankInfo.categoryName1" :imgSrc="baseRightsImgSrc" :htmlContent="bankInfo.categoryContent1" ></card-info-segment>
              <card-info-segment :desc="bankInfo.categoryName2" :imgSrc="baseRebateImgSrc" :htmlContent="bankInfo.categoryContent2" ></card-info-segment>
           </div>
          <div class="flow-class">
              <div> <img src="../../assets/images/process-icon.png"> <span>返利流程</span></div>
              <div class="flow-step">
                  <img src="../../assets/images/apply.png">
                  <img src="../../assets/images/next.png">
                  <img src="../../assets/images/write.png">
                  <img src="../../assets/images/next.png">
                  <img src="../../assets/images/great.png">
                  <img src="../../assets/images/next.png">
                  <img src="../../assets/images/get.png">
              </div>
              <div class="flow-step-text">
                  <span>申请卡片</span>
                  <span>官网填写</span>
                  <span>成功办卡</span>
                  <span>获得返利</span>
              </div>
          </div>
        </div>

        <div class="apply-class" @click="actionApply">
            立即申请
        </div>

        <card-info-dialog dialogTitle="办卡预登记" :dialogVisible="dialogPhoneVisible"  @bindHideDialog='bindHideDialog'  @defaultClick="defaultClick" @primaryClick="phoneClick">
            <div class="d-content-phone">
              <span>请输入您申请信用卡的手机号码</span>
              <input class="weui-input" type="tel" placeholder="请输入手机号" v-model="phoneNo" >
              <p @click="bindCheck"><i class="check" :class="{checked:isChecked}" ></i>返利说明:此返利仅限首次申请该行信用卡用户</p>
              </div>
        </card-info-dialog>
        <card-info-dialog dialogTitle="办卡预登记" :dialogVisible="dialogSmsVisible" @bindHideDialog='bindHideDialog'  @defaultClick="defaultClick" @primaryClick="smsClick">
            <div class="d-content-sms">
              <span>请输入您申请信用卡的手机号码</span>
              <input class="weui-input input-sms" type="tel" placeholder="请输入手机号" v-model="phoneNo">
              <div>
                <input class="weui-input" type="tel" placeholder="请输入图形验证码" v-model="verifyCode" maxlength="4">
                <!-- <span class="get-verify-btn" v-show="!verifyImg" @click="getVerifyImg">获取图形验证码</span> -->
                <div class="verify-img" v-show="verifyImg" @click="getVerifyImg"><img :src="verifyImg" alt="单击图片刷新" ></div>
              </div>
              <div>
                <input class="weui-input" type="tel" placeholder="请输入短信验证码" v-model="smsCode" maxlength="4">
                <span  class="get-verify-btn" @click="getSmsCode" >{{countDownStr}}</span>
              </div>
              <p>&nbsp;</p>
              <!-- <p><i class="check" :class="{checked:isChecked}" @click="bindCheck"></i>返利说明:此返利仅限首次申请该行信用卡用户</p> -->
            </div>
        </card-info-dialog>
        <top-bar :title="bankInfo.name"></top-bar>
        <FloatTool></FloatTool>
    </div>
</template>

<script>
import CardInfoSegment from '@/components/index/CardInfoSegment'
import CardInfoDialog from '@/components/index/CardInfoDialog'
import Toast from '@/components/index/Toast'
import topBar from '@/components/common/TopBar'
import {mapGetters} from 'vuex'
import FloatTool from '@/components/FloatTool.vue'
// import RGBaster from '@/utils/rbgaster'
import _ from 'lodash'
export default {
  components: { CardInfoSegment, CardInfoDialog, Toast, topBar, FloatTool },
  props: {},
  data () {
    return {
      isChecked: false,
      bankInfo: {
        id: '',
        icon: '',
        categoryName1: '',
        categoryContent1: '',
        categoryName2: '',
        categoryContent2: '',
        propaganda: '',
        name: '',
        rebate: 0,
        status: '',
        tag1: '',
        tag2: '',
        tag3: '',
        url: '',
        color: '',
        inners: 1
      },
      baseRightsImgSrc: require('../../assets/images/king_icon.png'),
      baseRebateImgSrc: require('../../assets/images/money_icon.png'),

      dialogPhoneVisible: false,
      dialogSmsVisible: false,
      verifyImg: '',
      verifyCode: '',
      smsCode: '',
      phoneNo: '',
      phoneNoTemp: '',
      toastShow: false,
      toastContent: '',
      countDownStr: '获取短信验证码',
      smsBtnDisable: false

    }
  },
  watch: {},
  computed: {
    showMid: function () {
      return this.categoryName1 || this.bankInfo.categoryName2
    }
  },
  methods: {
    bindCheck () {
      this.isChecked = !this.isChecked
    },
    ...mapGetters(['GETUSERINFO']),
    bindHideDialog () {
      this.dialogSmsVisible = false
      this.dialogPhoneVisible = false
    },
    loadImg: function () {
      var index = this.bankInfo.color || 0
      this.$refs.topBg.style.backgroundColor = this.getColor(this.bankInfo.color)
    },
    getColor: function (c) {
      // 0 默认值 1,2,3,4,5后台配置
      var colors = ['#2371FF', '#2371FF', '#FF3F46', '#43C6CD ', '#FF821A', ' #6E46DD']
      return colors[c]
    },
    defaultClick: function () {
      this.dialogPhoneVisible = false
      this.dialogSmsVisible = false
    },
    actionApply: _.debounce(function () {
      // 点击立即申请
      var uid = sessionStorage.getItem('isLogin')
      if (!uid) {
        this.$toast('请先登录')
        this.$router.push({name: 'login'})
      } else {
        // this.$toast('uid是：' + uid)
        this.dialogPhoneVisible = true
        let phone = this.$store.state.accountInfo.phoneNo || ''
        if (phone) {
          this.phoneNo = phone
        }
      }
    }, 500),
    phoneClick: _.debounce(function () {
      if (this.checkPhone()) {
        if (!this.isChecked) {
          this.$toast('请勾选返利说明')
          return
        }

        this.dialogPhoneVisible = false

        let phone = this.$store.state.accountInfo.phoneNo || ''
        if (this.phoneNo == phone) {
          // 登记号码为目前的注册账号 无需验证 直接进入下一步
          this.nextStep(false)
        } else {
          // 需要验证手机号码是否要输入验证码
          this.needCheckPhone()
        }
      }
    }, 500),
    async needCheckPhone () {
      var res = await this.$http.postData('/app/rebate/addreg/needCode', { phone: this.phoneNo, type: 2 })
      if (res && res.body && res.body.content && res.body.content.code) {
        // 弹出短信验证框
        this.dialogSmsVisible = true
        this.getVerifyImg()
      } else {
        // 不需要验证
        this.nextStep(false)
      }
    },
    checkPhone: function () {
      // 校验手机号格式
      var value = this.phoneNo
      var phonrReg = /^1\d{10}$/
      if (!value) {
        this.$toast('请输入手机号码')
        return false
      } else if (!phonrReg.test(value)) {
        this.$toast('请输入正确的手机号码')
        return false
      } else {
        return true
      }
    },
    getVerifyImg: _.debounce(async function () {
      // 请求获取短信验证码
      if (this.checkPhone()) {
        var res = await this.$http.postData('/app/imagecode/index2', { phone: this.phoneNo })
        if (res && res.body && res.body.content && res.body.content.image) {
          this.verifyImg = 'data:image/png;base64,' + res.body.content.image
        }
      }
    }, 500),
    onGetVerifyImg: function (res) {
      if (res.isSuccess === 0) {
        if (res.responseData.image) {
          this.verifyImg = 'data:image/png;base64,' + res.responseData.image
        }
      } else {
        this.showToast(res.responseData)
      }
    },
    getSmsCode: function () {
      // 获取短信验证码
      if (this.smsBtnDisable) {
        return
      }
      var res = /\d{4}/
      if (!res.test(this.verifyCode)) {
        this.$toast('请输入正确的验证码')
        return
      }
      if (this.checkPhone()) {
        let data = { phone: this.phoneNo, type: '3', imgcode: this.verifyCode }
        this.$http.postData('/app/captcha', data).then(res => {
          if (res && res.body && res.body.statusCode && res.body.statusCode === 1002) {
            this.$toast('验证码已发送到您的手机')
            this.smsCountDown()
          } else {
            this.$toast(res.body.statusMsg)
          }
        })
      } else {
        this.showToast('请输入图形验证码')
      }
    },
    smsCountDown: function () {
      if (!this.timer) {
        this.count = 60
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= 60) {
            this.smsBtnDisable = true
            this.count--
            this.countDownStr = this.count + 's'
          } else {
            this.smsBtnDisable = false
            clearInterval(this.timer)
            this.countDownStr = '重新发送'
            this.timer = null
          }
        }, 1000)
      }
    },
    smsClick: _.debounce(function () {
      if (this.checkPhone()) {
        if (this.smsCode) {
          this.nextStep(true)
        } else {
          this.$toast('请输入短信验证码')
        }
      }
    }, 500),
    async nextStep (needSmsCode) {
      let params = {type: 2, p2p_name: this.bankInfo.name, reg_account: this.phoneNo}
      if (needSmsCode) {
        params.code = this.smsCode
      }
      var res = await this.$http.postData('/app/rebate/addregNew', params)
      if (res && res.body && res.body.statusCode === 0) {
        this.$router.push({name: 'webview', query: {url: this.bankInfo.url, name: this.bankInfo.name}})
      } else {
        this.$toast(res.body.statusMsg)
      }
    },
    dismiss: function () {
      this.toastShow = false
    }
  },
  created () {
    // var info = this.GETBANKINFOSTATE()

    this.bankInfo = JSON.parse(sessionStorage.getItem('bankInfo')) || ''
    this.phoneNo = this.GETUSERINFO().phone || ''
  },
  mounted () {
    this.loadImg()
  }
}
</script>

<style lang="postcss" scoped>
input{
  -webkit-appearance: none;
}
.bk-info-container {
  padding-top: 35px;
  display: flex;
  flex-direction: column;
  color: #1c2042;
  height: 100%;
  overflow: auto;
  & .bank-top {
    width: 100%;
    height: 70px;
    display: flex;
    flex-shrink: 0;
    align-items: center;
    padding: 15px;
    & .top-right {
      height: 44px;
      margin-left: 10px;
      overflow: hidden;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      & .top {
        display: flex;
        align-items: flex-end;
        height: 22px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        height: 22px;
      }
    }

    & span {
      color: #ffffff;
    }

    & .bank-name {
      font-size: 16px;
      height: 16px;
      line-height: 16px;
    }

    & .card-img {
      width: 44px;
      height: 44px;
      border-radius: 22px;
      background-color: rgba(255, 255, 255, 0.8);
      display: flex;
      justify-content: center;
      align-items: center;
      & > img{
        width: 42px;
        height: 42px;
        border-radius: 21px;
      }
    }
    & .rights {
      font-size: 12px;
      height: 16px;
      line-height: 16px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    & .bank-divider {
      font-size: 12px;
      margin-left: 6px;
      height: 16px;
      line-height: 16px;
      margin-right: 6px;
    }
    & .tag-class {
      display: flex;
      & > span {
        height: 16px;
        line-height: 16px;
        font-size: 10px;
        border-radius: 8px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        text-align: center;
        padding-left: 5px;
        padding-right: 5px;
        border: 1px solid #ffffff;
      }
      & :not(:nth-child(1)) {
        margin-left: 5px;
      }
    }
  }

  & .mid-class {
    margin-top: 18px;
  }

  & .flow-class {
   
    border-top: 8px solid #f6f7f8;
    margin-bottom: 18px;
    background-color: #fff;
    & > div {
      padding: 10px 23px;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      & > img {
        width: 20px;
        height: 20px;
      }
      & > span {
        font-size: 14px;
        color: #1c2042;
        line-height: 20px;
        margin-left: 5px;
        font-weight: bold;
      }
    }

    & .flow-step {
      display: flex;
      justify-content: space-between;
      border-top: 1px solid #f6f7f8;
      & :nth-child(odd) {
        width: 48px;
        height: 48px;
      }
      & :nth-child(even) {
        width: 12px;
        height: 12px;
      }
    }
    & .flow-step-text {
      display: flex;
      justify-content: space-between;
      padding: 0px;
      margin-left: 20px;
      margin-right: 20px;
      margin-bottom: 48px;
      & > span {
        font-size: 12px;
        color: #1c2042;
        font-weight: normal;
      }
    }
  }

  & .apply-class {
    background-color: #4477dd;
    width: 100%;
    height: 48px;
    font-size: 18px;
    line-height: 48px;
    color: #ffffff;
    text-align: center;
    position: fixed;
    bottom: 0px;
  }

  & .d-content-phone {
    margin-top: 10px;
    text-align: left;
    margin-bottom: 10px;
    & > span {
      font-size: 12px;
      color: #353543;
    }
    &>p{
      font-size: 11px;
      color: #D0021B;
      padding-top: 11px;
      padding-bottom: 10px;
    }

    & .weui-input {
      margin-top: 12px;
      border: 1px solid #d6d6d6 !important;
      border-radius: 4px;
      height: 42px;
      line-height: 1;
      padding-left: 8px;
      padding-right: 8px;
      width: 100%;
    }
  }
  & .d-content-sms {
    margin-top: 10px;
    text-align: left;
    margin-bottom: 10px;
    &>p{
      padding-top: 7px;
      font-size: 11px;
      color: #D0021B;
    }
    & .verify-img {
      width: 110px;
      border: none;
      height: 30px;
      position: absolute;
      top:50%;
      right: 10px;
      transform: translateY(-50%);
      border-radius: 3px;
      overflow: hidden;
      & > img{
         border: none;
         width: 110px;
        height: 30px;
      }
    }

    & .get-verify-btn {
      color: #1c2042;
      height: 26px;
      line-height: 26px;
      text-align: center;
      font-size: 12px;
      border: 1px solid #1c2042;
      border-radius: 4px;
      padding-left: 2px;
      padding-right: 2px;
      margin-right: 10px;
      width: 110px;
    position: absolute;
    top:50%;
    right: 0;
    background-color: #fff;
    transform: translateY(-50%);
    }

    & .input-sms {
      margin-top: 12px;
      border: 1px solid #d6d6d6 !important;
      border-radius: 4px;
      height: 42px;
      line-height: auto;
      padding-left: 8px;
      padding-right: 8px;
    }
    & > span {
      font-size: 12px;
      color: #353543;
    }
    & > div {
      border: 1px solid #d6d6d6 !important;
      height: 42px;
      border-radius: 4px;
      margin-top: 12px;
      display: flex;
      align-items: center;
      position: relative;
    }
    & .weui-input {
      width: 100%;
      border: 0px;
      height: 40px;
      line-height: 1;
      padding-top: 8px;
      padding-bottom: 8px;
      padding-left: 8px;
      padding-right: 8px;
    }
  }
  & .check{
    display: inline-block;
    height: 13px;
    width:13px;
    background: url(../../assets/images/loan_checkbox_unselected.png) center top no-repeat;
    background-size: contain;
    position: relative;
    top:1px;
    margin-right: 5px;
  }
  & .checked{
    background: url(../../assets/images/loan_checkbox_selected.png) center top no-repeat;
    background-size: contain;
  }
}

</style>



