<template>
    <div class="dialog-cover" v-show="dialogVisible" @click="bindHideDialog">
      <div class="dialog-container"  @click.stop>
            <div class="weui-mask"></div>
            <div class="weui-dialog">
                <div class="weui-dialog__hd"><strong class="weui-dialog__title">{{dialogTitle}}</strong></div>
                <div class="weui-dialog__bd">
                    <slot></slot>
                </div>
                <div class="weui-dialog__ft">
                    <div @click="defaultClick" class="weui-dialog__btn weui-dialog__btn_default">{{defaultStr || "取消"}}</div>
                    <div @click="primaryClick" class="weui-dialog__btn weui-dialog__btn_primary">{{primaryStr || "确定"}}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  components: {},
  props: [
    'dialogVisible',
    'dialogTitle',
    'defaultStr',
    'primaryStr'
  ],
  data () {
    return {
      dialogShow: ''
    }
  },
  watch: {},
  computed: {},
  methods: {
    defaultClick: function () {
      // 点击取消
      this.$emit('defaultClick')
    },
    primaryClick: function () {
      // 点击确定
      this.$emit('primaryClick')
    },
    bindHideDialog () {
      this.$emit('bindHideDialog')
    }
  },
  created () {
  },
  mounted () {}
}
</script>
<style lang="postcss" scoped>
.dialog-cover{
  position: fixed;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  z-index: 10;
}
.dialog-container {
  width: 296px;
  position: absolute;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
  background-color: #fff;
  padding:0 16px;
  z-index: 10;
  border-radius: 10px;
  & .weui-dialog {
    border-radius: 10px;
    & .weui-dialog__hd {
      padding: 20px;
      
      text-align: center;
      & .weui-dialog__title {
        font-size: 20px;
        color: #353543;
        font-weight: 600;
      }
    }

    & .weui-dialog__ft {
      display: flex;
      justify-content: space-between;
      padding-bottom: 17px;


      & .weui-dialog__btn_default {
        background: #c8c9ce;
        font-size: 18px;
        color: #ffffff;
        line-height: 40px;
        height: 40px;
        border-radius: 4px;
        width: 120px;
        height: 40px;
        text-align:center;
      }

      & .weui-dialog__btn_primary {
        background: #4477dd;
        font-size: 18px;
        color: #ffffff;
        line-height: 40px;
        height: 40px;
        border-radius: 4px;
       
        width: 120px;
        height: 40px;
        text-align:center;
      }
    }
    & .weui-dialog__ft:after {
      border: none;
    }

    & .weui-dialog__btn:after {
      border: none;
    }
  }
}
</style>



